<template>
  <div id="app">
    <navbar v-if="!isMobile"/>
    <!--    <div class="container">-->
    <el-container class="container">
      <router-view
        v-if="!isMobile || $route.meta.equal"
        v-wechat-title="$route.meta.title"
      />
      <router-view
        v-if="isMobile && !$route.meta.equal"
        v-wechat-title="$route.meta.title"
        name="m"
      />
    </el-container>
    <!--    </div>-->
    <tail v-if="!isMobile"/>
  </div>
</template>

<script>

export default {
  name: 'App',
  components: {
    navbar: () => import('@/components/NavBarPC.vue'),
    tail: () => import('@/components/TailBarPC.vue')
  },
  beforeCreate () {
    this.$store.commit('initializeStore')
    if (this.$store.state.token) {
      this.$axios.defaults.headers.common.Authorization = 'Token ' + this.$store.state.token
    } else {
      this.$axios.defaults.headers.common.Authorization = ''
    }
  },

  async created () {
    // 获取用户数据
    if (this.$store.state.isAuthenticated) {
      await this.$axios
        .get('/api/v1/users/me/')
        .then(response => {
          this.$store.commit('setMe', response.data)
          // console.log(this.$store.state.me)
        })
        .catch(err => {
          try {
            const data = err.response.data
            let errors = '<p>获取用户数据错误</p>'
            if (typeof data === 'string') {
              errors += '<p>' + data + '</p>'
            } else {
              for (const key in data) {
                errors += '<p>' + data[key] + '</p>'
              }
            }
            this.$message({
              // title: '获取用户数据错误',
              dangerouslyUseHTMLString: true,
              message: errors,
              type: 'error'
            })
          } catch (e) {
            this.$message({
              dangerouslyUseHTMLString: true,
              message: '<p>获取用户数据错误</p><p>' + err + '</p>',
              type: 'error'
            })
          }
        })
    }
  },
  data () {
    return {}
  },
  computed: {
    isMobile () {
      return window.matchMedia('(max-width: 450px)').matches
    }
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  top: 0;
  left: 0;
}

* {
  margin: 0;
  padding: 0;
  font-family: '微软雅黑';
  box-sizing: border-box;
}

a {
  text-decoration: none;
  color: #524848;
}

body {
  background: #f6f6f6 url(/images/bg.gif);
  overflow-x: auto;
}

[class^="el-icon-ali"], [class*="el-icon-ali"] {
  font-family: "iconfont" !important;
  font-size: 16px;
  font-style: normal;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.container {
  min-height: 600px;
  width: 100%;
  position: relative;
  margin: 50px 0;
  justify-content: center;
}

.sprite {
  background: url(/images/sprite.png) no-repeat;
  background-size: 600% 1100%;
  background-position: 20% 0%;
}

.clear {
  clear: both;
}
</style>



