import Router from 'vue-router'

import Vue from 'vue'
import NProgress from 'nprogress'
import 'nprogress/nprogress.css'
import store from '@/store'

Vue.use(Router)
/*

单页面组件
---
component: () => import('./components/name.vue'),
---
双页面组件
---
components: {
  default: () => import('./components/namePC.vue'),
  m: () => import('./components/nameM.vue')
},
---

*/
const routes = [
  {
    path: '/',
    name: 'home',
    components: {
      default: () => import('@/views/HomeViewPC.vue'),
      m: () => import('@/views/HomeViewM.vue')
    },
    meta: {
      title: 'Sakuyark'
    }
  },
  {
    path: '/login',
    name: 'login',
    component: () => import('@/views/LogIn.vue'),
    meta: {
      title: '登录',
      equal: true
    }
  },
  {
    path: '/signup',
    name: 'signup',
    component: () => import('@/views/SignUp.vue'),
    meta: {
      title: '注册',
      equal: true
    }
  },
  {
    path: '/activation/:uid/:token',
    name: 'activate',
    component: () => import('@/views/ActivationView.vue'),
    meta: {
      title: '用户激活',
      equal: true
    }
  },
  {
    path: '/settings',
    name: 'settings',
    components: {
      default: () => import('@/views/SettingsViewPC.vue')
      // m: () => import('@/views/NotFoundM.vue')
    },
    meta: {
      title: '个人设置'
    }
  },
  {
    path: '/:catchAll(.*)',
    name: 'NotFound',
    components: {
      default: () => import('@/views/NotFoundPC.vue'),
      m: () => import('@/views/NotFoundM.vue')
    },
    meta: {
      title: '404 Not Found'
    }
  }
]

const router = new Router({
  mode: 'history',
  routes
})

NProgress.configure({
  easing: 'ease',
  speed: 500,
  showSpinner: false,
  trickleSpeed: 200,
  minimum: 0.3
})

router.beforeEach((to, from, next) => {
  NProgress.start()
  if (to.matched.some(record => record.meta.requireLogin) && !store.state.isAuthenticated) {
    next('/login')
  } else {
    next()
  }
})
router.afterEach(() => {
  NProgress.done()
})

export default router



